@import "./util.scss";

body {
  overflow-x: hidden;
  //word-spacing: -10px;
}

//sectionslice//
.landing {
  display: flex;
  flex-direction: column;
  .sectionslice {
    margin-top: 10%;
    border: solid 1px #{$logocolor};
    border-radius: 2px;
    border-left: none;
    width: 65%;
    display: flex;
    justify-content: flex-end;

    .triangle {
      width: 2vw;
      border-top: 3.2vh solid transparent;
      border-right: 5vh solid #{$logocolor};
      border-bottom: 3.2vh solid transparent;
    }
  }

  .slicegreen {
    margin-top: 10%;
    border: solid 1px #{$green};
    border-left: none;
    .triangle {
      border-right: 5vh solid #{$green};
    }
  }

  .slicered-left {
    margin-top: 15%; //늘림
    border: solid 1px #{$red};
    border-left: none;
    .triangle {
      border-right: 5vh solid #{$red};
    }
  }

  .slicered {
    margin-top: 60px;
    float: right;
    border: solid 1px #{$red};
    border-right: transparent;
    justify-content: flex-start;

    .triangle-right {
      width: 0px;
      height: 0px;
      border-top: 3.2vh solid transparent;
      border-left: 5vh solid #{$red};
      border-right: 4vh solid none;
      border-bottom: 3.2vh solid transparent;
    }
  }

  .sliceblue {
    margin-top: 10%; //줄임
    float: right;
    border: solid 1px #{$logocolor};
    border-right: transparent;
    //display: flex;
    justify-content: flex-start;

    .triangle-right {
      border-top: 3.2vh solid transparent;
      border-left: 5vh solid #{$logocolor};
      //border-right: 3.5vh solid none;
      border-bottom: 3.2vh solid transparent;
    }
  }
}

///////////inner--first/////////
.landing {
  max-width: 100vw;
  display: flex;
  flex-direction: column;
  color: #{$logocolor};

  //border: solid 1px #{$logocolor};

  &__inner {
    //border: solid 1px red;
    width: 100vw;
  }

  &__inner--first {
    //border: solid 1px tomato;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 15%;

    .first-wrap {
      //border: solid 1px #000;
      display: flex;
      width: 89vw;
      justify-content: center;
      align-items: center;

      .introtext {
        //border: solid 1px green;
        text-align: center;

        h1 {
          font-size: 5.5vw;
          margin-top: 0;
          margin-bottom: 0;
        }

        p {
          font-size: 1.7vw;
          font-weight: 700;
          margin-top: 2%;
          word-break: keep-all;
          word-spacing: -7px;
          //line-height: 2.3rem;
        }

        button {
          font-family: "IBM Plex Mono";
          font-weight: 700;
          width: 70%;
          //height: 60px;
          padding: 3.7% 0;
          background-color: #{$logocolor};
          border: none;
          border-radius: 2px;
          color: white;
          font-size: 1.8vw;
          text-align: center;
          margin-top: 13%;
          margin-bottom: 8%;
          word-spacing: -5px;

          &:hover {
            transform: scale(1.03);
            box-shadow: #{$btnboxshadow};
            cursor: pointer;
          }
        }
      }

      img {
        width: 48vw;
        margin-left: 6%;
        //box-shadow: #{$boxshadow};
        box-shadow: 0px 10px 20px 10px rgba(0, 0, 0, 0.1);
        box-sizing: border-box;
        //border: solid 3px red;
      }
      .mobile-introtext {
        display: none;
      }
    }
  }
}

//inner--second//
.landing {
  &__inner--second {
    //border: solid 1px tomato;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 8%;

    .desc-wrap {
      width: 85vw;
      //border: solid 1px #000;
      display: flex;
      justify-content: center;
      //margin-top: 3%;

      img {
        width: 30%;
        box-sizing: border-box;
      }

      p {
        font-size: 1.7vw;
        font-weight: 700;
        margin-top: 10%;
        word-break: keep-all;
        width: fit-content;
        margin-top: 10%;
        word-spacing: -5px;
      }
    }

    .desc-wrap--1 {
      img {
        margin-right: 10vw;
      }
      p {
        margin-top: 5%;
        margin-left: -8%;
        margin-right: 25%;
      }
    }
    .desc-wrap--2 {
      img {
        width: 39%;
      }
      p {
        margin-top: 11%;
        margin-left: 10%; //
        margin-right: 6%;
        //background-color: yellowgreen;
      }
    }

    .desc-wrap--3 {
      img {
        margin-right: 5vw;
        margin-left: -12vw;
        margin-top: 4%;
      }
      p {
        margin-top: 20%;
        //margin-left: 0%;
      }
    }
  }
}

//inner--third//
.landing {
  &__inner--third {
    //border: solid 1px tomato;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 20%; //늘림
    font-size: 23px; //사이즈 줄임

    h1 {
      text-align: center;
      padding: 0;
      margin: 0;
      font-size: 3.2vw;
      font-weight: 800;
      word-spacing: -8px;
      white-space: nowrap;
    }

    .desc {
      //border: solid 1px blue;
      display: flex;
      justify-content: center;
      margin-top: 8%;
      align-items: center;
      word-break: keep-all;
      //white-space: nowrap;

      &__inner {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 80%;
        //margin-left: 2.5%;
        //border: solid 3px #000;

        img {
          //border: solid 1px tomato;
          width: 30vw;
          margin: 1.5%;
         //box-shadow: #{$boxshadow};
          box-shadow: 0px 10px 20px 10px rgba(0, 0, 0, 0.1);
          box-sizing: border-box;
        }

        .desc-text {
          //border: solid 3px tomato;
          margin-left: 6%;

          h2 {
            margin: 0;
            //margin-top: 1%; //줄임
            font-size: 2.8vw;
            font-weight: 800;
            word-spacing: -8px;
            white-space: nowrap;
          }

          p {
            font-size: 1.7vw;
            word-spacing: -5px;
            white-space: nowrap;
            //background-color: blanchedalmond;
            margin-bottom: 1%;
          }

          a {
            font-size: 1.6vw;
            word-spacing: -5px;
            color: #{$logocolor};
            text-shadow: #{$btnboxshadow};
            //background-color: blanchedalmond;

            &:hover {
              text-shadow: 6px 2px 2px rgba(172, 166, 166, 0.445);
              cursor: pointer;
            }
          }
        }
      }
    }

    .desc-dragdrop {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 10%;
      //border: solid 1px blue;

      .drag-top-desc {
        text-align: center;
        font-size: 1.5vw;
        margin-bottom: 3%;
        word-spacing: -7px;
        font-weight: 700;
      }

      img {
        display: block;

        width: 80vw;
        //margin-left: 1%; //
        //border: solid 1px tomato;
        //box-shadow: #{$boxshadow};
        box-shadow: 0px 10px 20px 10px rgba(0, 0, 0, 0.1);
        box-sizing: border-box;
      }

      &__desc {
        display: flex;
        flex-direction: column;
        //border: solid 1px yellowgreen;
        width: 78vw;
        margin-top: 4%; //줄임
        font-size: 2.8vw;
        font-weight: 800;
        word-spacing: -5px;
        margin-left: 1vw;
        //line-height: 1em;
        //white-space: nowrap;
      }
      p {
        margin: 10px;
        //font-size: 2vw;
      }
      .desc1 {
        text-align: left;
      }

      .desc2 {
        text-align: right;
        margin-top: -10px;
      }
    }
  }
}

//inner--fourth//
.landing {
  &__inner--fourth {
    //background-color: red;
    .alarm-wrapper {
      //border: solid 1px tomato;
      margin-top: 10%;
      margin-bottom: 7%;
      .jello-horizontal{
        margin-right: 2%;
      }
      .alarm {
        max-width: 65%;
        height: 70px;
        margin-left: 15%;
        align-items: center;
        line-height: 70px;
        text-align: center;
        font-size: 2.3vw;
        font-weight: 700;
        white-space: nowrap;
        background-color: #{$alarmcolor};
        box-shadow: #{$boxshadow};
      }
    }

    .alarm-desc-wrapper {
      //margin-top: 10%;
      display: flex;
      // align-items: center;
      justify-content: center;
      width: 83vw;
      //border: solid 1px red;
      margin: 0 auto;
      .alarm-desc {
        text-align: center;
        white-space: nowrap;
        margin-top: 9%;
        //border: solid 1px red;

        &__inner {
          //margin-left: 55px;
          //border: solid 1px red;
          word-spacing: -5px;
          h2 {
            font-size: 2.7vw;
            word-spacing: -8px;
            font-weight: 800;
          }
          p {
            font-size: 1.7vw;
            margin-bottom: 3%;
            word-spacing: -6px;
          }
          a {
            font-size: 1.6vw;
            color: #{$logocolor};
            text-shadow: #{$btnboxshadow};
            line-height: 0;

            &:hover {
              text-shadow: 6px 2px 2px rgba(172, 166, 166, 0.445);
              cursor: pointer;
            }
          }
        }
      }
      .alarm-img {
        //border: solid 1px #000;
        min-width: 40%;
        // height: 400px;
        margin-left: 4%;
        //box-shadow: #{$boxshadow};
        img {
          margin: 0 auto;
          width: 100%;
        }
      }
    }
  }
}

//inner--fifth//
.landing {
  &__inner--fifth {
    //border: solid 1px green;
    margin-top: 25%;
    display: flex;
    flex-direction: column;
    align-items: center;

    .profile-wrapper {
      width: 88vw;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      //border: solid 2px rgb(206, 32, 32);

      .landing__searchProfile {
        display: flex;
        flex-direction: column;
        justify-content: center;
        border: solid 1px #000;
        text-align: center;
        width: 21vw;
        aspect-ratio: 3/4;
        box-shadow: #{$boxshadow};
        &__profileImage {
          margin-top: -8%;
          font-size: 7vw;
        }
        p {
          margin-top: 5%;
          font-size: 2vw;
          font-weight: 700;
        }

        &__socialInfo {
          font-size: 1vw;
          display: flex;
          justify-content: space-around;
        }

        &__companyInfo {
          margin-top: 10%;
          font-size: 1vw;
          div {
            margin: 3px;
          }
        }
      }
    }
    .collection-desc {
      margin-top: 5%; //줄임
      text-align: center;
      font-size: 2.8vw;
      font-weight: 800;
      word-spacing: -7px;

      a {
        font-size: 1.7vw;
        font-weight: 400;
        color: #{$logocolor};
        text-shadow: #{$btnboxshadow};

        &:hover {
          text-shadow: 6px 2px 2px rgba(172, 166, 166, 0.445);
          cursor: pointer;
        }
      }

      // @include mobile {
      //   font-size: 4.7vw;

      //   a {
      //     font-size: 3.5vw;
      //   }
      // }
    }
  }
}

//inner--sixth//
.landing {
  &__inner--sixth {
    display: flex;
    flex-direction: column;
    align-items: center;
    //border: solid 1px blue;
    margin-bottom: 110px;

    .try-wrapper {
      margin-top: 100px;
      margin-left: 2%;
      margin-bottom: 5%;
      font-size: 3.6vw;
      word-spacing: -9px;
      font-weight: 800;
      white-space: nowrap;
    }

    .values-wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
      margin-bottom: 8%;
      word-spacing: -5px;
      word-break: keep-all;
      font-weight: 600;

      &__up {
        display: flex;
        width: 75vw;
        justify-content: center;

        .values-up {
          width: 20vw;
          border: solid 1px #000; //테두리
          margin-left: 1.5%;
          font-size: 1.7vw;
          padding: 7px;
          text-align: center;
          box-shadow: #{$btnboxshadow};
        }
      }
      &__down {
        display: flex;
        width: 85vw;
        justify-content: center;
        margin-top: 2%;
        .values--down {
          border: solid 1px #000; //테두리
          margin-left: 1.5%;
          font-size: 1.7vw;
          width: 60%;
          padding: 7px;
          text-align: center;
          box-shadow: #{$btnboxshadow};
          white-space: nowrap;
        }
      }
    }

    .button-bottom {
      //border: solid 3px red;
      font-family: "IBM Plex Mono";
      width: 29vw;
      //height: 60px;
      padding: 4.5% 0;
      background-color: #{$logocolor};
      border: none;
      border-radius: 2px;
      color: white;
      font-size: 2.5vw;
      font-weight: 600;
      text-align: center;

      &:hover {
        transform: scale(1.03);
        box-shadow: #{$btnboxshadow};
        cursor: pointer;
      }
    }
  }
}

